import React, { Component } from 'react'
import { Container, FormGroup, Form, Media, Input, Row, Col, Button, Navbar, NavbarBrand, ButtonGroup } from 'reactstrap'
import FaWhatsapp from 'react-icons/lib/fa/whatsapp'
import FaGlobe from 'react-icons/lib/fa/globe'
import _ from 'lodash'

import 'bootstrap/dist/css/bootstrap.min.css'

const MAX_RESULTS = 20

class App extends Component {
	state = {
		initialItems: [],
		task: '',
		tasks: [],
		tasksMap: {},
		items: []
	}

	componentDidMount() {
		let _this = this

		fetch('https://raw.githubusercontent.com/gyms-aps/gyms-aps.github.io/master/tasks.json')
			.then(results => results.json())
			.then((tasks) => {
				let tasksMap = _.keyBy(tasks, 'key')
				_this.setState({ tasksMap, tasks })
				return {}
			})
			.then(() => {
				fetch('https://raw.githubusercontent.com/gyms-aps/gyms-aps.github.io/master/portals.json')
					.then(results => results.json())
					.then((initialItems) => {
						_this.setState({ initialItems })

						this.applyFilterList('')
					})
			})
	}

	filterList = (event) => {
		let filter = String(_.get(event, 'target.value')).toLowerCase()

		this.setState({ filter })
		this.applyFilterList(filter)
	}

	changeTask = (event) => {
		let task = event.target.value

		this.setState({ task })
	}

	applyFilterList = (filter) => {
		const { initialItems } = this.state

		let updatedList = initialItems.filter(function(item) {
				let val = String(filter).toLowerCase()
				return _.get(item, 'descricao_text', '').toLowerCase().search(val) !== -1
			});
		this.setState({items: (_.size(filter) > 5) ? updatedList : updatedList.slice(0, MAX_RESULTS)});
	}

	whatsappUrl = (row) => {
		let task = this.state.tasksMap[this.state.task],
			taskText = (!task || task.ignore) ? '' : `_${task.text}_\r\n\r\n`,
			googleLink = `https://maps.google.com/maps/?daddr=${row.latitude},${row.longitude}`,
			shareText = encodeURIComponent(`${taskText}*${row.descricao}* \r\n ${googleLink} `)
		return `https://api.whatsapp.com/send?text=${shareText}`
	}

	render() {
		const { tasks } = this.state

		return (
			<div>
				<Navbar color='dark' dark expand='md'>
					<NavbarBrand href='/'> Ginásios/Pokestops - Anápolis </NavbarBrand>
				</Navbar>
				<Container className='container'>
					<div className='filter-list'>
						<Row>
							<Col md={{ size: 8, offset: 2 }} xs={12}>
								<Input
									autoFocus={true}
									placeholder='Pesquise' 
									onChange={this.filterList} />
							</Col>
						</Row>
						<Row style={{marginTop: '10px'}}>
							<Col md={{ size: 8, offset: 2 }} xs={12}>
								<Form>
									<FormGroup row>
										<Col sm={12}>
											<select onChange={this.changeTask} className='form-control'>
												{
												tasks.map((task, index) => (
													<option key={index} value={task.key}>{task.text}</option>
												))
												}
											</select>
										</Col>
									</FormGroup>
								</Form>
							</Col>
						</Row>
					
						{
							this.state.items.map((row, index) => (
								<Row key={index}>
									<Col md={{ size: 8, offset: 2 }} sm={12} xs={12}>
										<Media key={index} style={{padding: '15px'}}>
											<Media left middle>
												<Media object src={row.image} alt="Imagem" style={{borderRadius: '50%', width: '100px', height: '100px', objectFit: 'cover'}} />
											</Media>

											<Media body  style={{paddingLeft: '15px'}}>
												<Media heading style={{fontSize: '22px'}}>
													{row.descricao}
												</Media>
												<Row>
													<Col sm={12} xs={12} md={8} style={{fontSize: '15px'}}>
														<strong>Endereço:</strong> {row.endereco}
													</Col>

													<Col sm={12} xs={12} md={4}>
														<ButtonGroup className='pull-right' size='lg'>
															<Button color="default" href={`//maps.google.com/maps?daddr=${row.latitude},${row.longitude}`} target='blank'>
																<FaGlobe />
															</Button>

															<Button color="default" href={this.whatsappUrl(row)} target='blank'>
																<FaWhatsapp style={{color: 'green'}}/>
															</Button>
														</ButtonGroup>
													</Col>
												</Row>
											</Media>
										</Media>
									</Col>
								</Row>
							))
						}
					</div>
				</Container>
			</div>
		);
	}
}

export default App;



